import ECharts from 'vue-echarts'
import 'echarts/lib/chart/pie'
import 'echarts/lib/chart/bar'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/title'
import { postProgram, getUsers } from '@/api/api'
export default {
  name: 'Start',
  components: {
    ECharts
  },
  data () {
    return {
      formLayout: 'vertical',
      polar: {
        title: {
          text: '',
          subtext: '',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          show: true,
          orient: 'vertical',
          left: 'left',
          data: ['', '', '', '', '']
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: '55%',
            center: ['50%', '60%'],
            data: [
              {value: 335, name: ''},
              {value: 310, name: ''},
              {value: 234, name: ''},
              {value: 135, name: ''},
              {value: 1548, name: ''}
            ],
            itemStyle: {
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      },
      orgOptions: {
        title: {
          text: 'ECharts ',
          subtext: '',
          x: 'center'
        },
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      },
      users: [],
      selectedUsers: [],
      team: []
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      getUsers().then((res) => {
        // console.log(res)
        this.users = res.data
      })
    },
    changeTeam (selected) {
      console.log(selected)
      // console.log(this.selectedUsers)
      const _this = this
      selected.forEach(function (item) {
        // if () {
        // }
        // this.team.push()
        console.log(item)
        for (let i = 0; i < _this.users.length; i++) {
          if (_this.users[i].id === item) {
            _this.team.push(_this.users[i])
          }
        }
      })
      console.log(this.team)
    },
    initCharts () {
      this.orgOptions = {
        title: {
          text: 'ECharts '
        },
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      }
    },
    onSubmit () {
      const data = {hello: 'hell', test: 'test'}
      postProgram(data).then((res) => {
        console.log(res)
      })
    }
  }
}



